'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { useLanguage } from '@/lib/i18n'
import { DashboardLayout } from '@/components/dashboard-layout'
import { ChevronDown, ChevronUp } from 'lucide-react'

interface LocationData {
  wilayas: Array<{
    id: number
    nom_fr: string
    nom_ar: string
    moughataas: Array<{
      id: number
      nom_fr: string
      nom_ar: string
      communes: number
    }>
  }>
}

export default function LocationsPage() {
  const [user, setUser] = useState<any>(null)
  const [locations, setLocations] = useState<LocationData>({ wilayas: [] })
  const [expanded, setExpanded] = useState<Set<number>>(new Set())
  const router = useRouter()
  const { t, language } = useLanguage()

  useEffect(() => {
    const token = localStorage.getItem('token')
    const userData = localStorage.getItem('user')

    if (!token || !userData) {
      router.push('/login')
      return
    }

    const parsedUser = JSON.parse(userData)
    if (parsedUser.role !== 'admin') {
      router.push('/dashboard')
      return
    }

    setUser(parsedUser)
    loadLocations()
  }, [router])

  const loadLocations = async () => {
    // Demo data structure
    setLocations({
      wilayas: [
        {
          id: 1,
          nom_fr: 'Alger',
          nom_ar: 'الجزائر',
          moughataas: [
            {
              id: 1,
              nom_fr: 'Alger Centre',
              nom_ar: 'الجزائر وسط',
              communes: 3,
            },
            {
              id: 2,
              nom_fr: 'Bab El Oued',
              nom_ar: 'باب الواد',
              communes: 2,
            },
          ],
        },
        {
          id: 2,
          nom_fr: 'Oran',
          nom_ar: 'وهران',
          moughataas: [
            {
              id: 3,
              nom_fr: 'Oran Centre',
              nom_ar: 'وهران وسط',
              communes: 4,
            },
          ],
        },
      ],
    })
  }

  const toggleExpanded = (id: number) => {
    const newExpanded = new Set(expanded)
    if (newExpanded.has(id)) {
      newExpanded.delete(id)
    } else {
      newExpanded.add(id)
    }
    setExpanded(newExpanded)
  }

  if (!user) return null

  return (
    <DashboardLayout user={user}>
      <div className="space-y-6">
        <div>
          <h1 className="text-3xl font-bold text-white">
            {t('locations.title')}
          </h1>
          <p className="text-slate-400 mt-1">{t('locations.subtitle')}</p>
        </div>

        <div className="bg-slate-800 border border-slate-700 rounded-lg overflow-hidden">
          <div className="space-y-2 p-4">
            {locations.wilayas.map(wilaya => (
              <div key={wilaya.id} className="border border-slate-700 rounded">
                <button
                  onClick={() => toggleExpanded(wilaya.id)}
                  className="w-full px-4 py-3 flex items-center justify-between hover:bg-slate-700/50 transition"
                >
                  <span className="flex items-center gap-2 text-white font-semibold">
                    {expanded.has(wilaya.id) ? (
                      <ChevronUp className="w-5 h-5" />
                    ) : (
                      <ChevronDown className="w-5 h-5" />
                    )}
                    {language === 'fr' ? wilaya.nom_fr : wilaya.nom_ar}
                  </span>
                  <span className="text-slate-400 text-sm">
                    {wilaya.moughataas.length}{' '}
                    {language === 'fr' ? 'Moughataas' : 'محافظات'}
                  </span>
                </button>

                {expanded.has(wilaya.id) && (
                  <div className="bg-slate-700/30 border-t border-slate-700 divide-y divide-slate-700">
                    {wilaya.moughataas.map(moughataa => (
                      <div
                        key={moughataa.id}
                        className="px-8 py-3 flex justify-between items-center"
                      >
                        <span className="text-slate-300">
                          {language === 'fr'
                            ? moughataa.nom_fr
                            : moughataa.nom_ar}
                        </span>
                        <span className="text-slate-400 text-sm">
                          {moughataa.communes}{' '}
                          {language === 'fr' ? 'Communes' : 'بلديات'}
                        </span>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    </DashboardLayout>
  )
}
